import { newSpecPage } from '@stencil/core/testing';
import { PrestoVimeo } from '../presto-vimeo';

describe('presto-vimeo', () => {
  it('renders', async () => {
    const page = await newSpecPage({
      components: [PrestoVimeo],
      html: `<presto-vimeo src="https://vimeo.com/532967387"></presto-vimeo>`,
    });
    expect(page.root).toEqualHtml(`
      <presto-vimeo src="https://vimeo.com/532967387">
        <div class="plyr__video-embed" data-plyr-embed-id="532967387" data-plyr-provider="vimeo" part="embed"></div>
      </presto-vimeo>
    `);
  });
});
